* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.card {
  border: 1px solid #f3f3f3;
  max-width: 200px;
}

.card__container {
  flex-direction: column;
  padding: 32px 16px 16px;
  display: flex;
}

.card__photo {
  justify-content: center;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__photo--margin {
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title--margin {
  margin-bottom: 4px;
}

.card__subtitle--margin {
  margin-bottom: 16px;
}

.card__subtitle {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  height: 40px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  transition: background-color .3s linear, color .3s linear;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.feedback {
  justify-content: space-between;
  display: flex;
}

.feedback__review {
  color: #060b35;
  align-self: center;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.feedback--margin {
  margin-bottom: 24px;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star--5 {
  background-image: url("star.822d561e.svg");
}

.price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.price--margin {
  margin-bottom: 16px;
}

